<template>
  <div class="wrap">
    <app-header title="重卡汇车联网平台App下载" :show-back="true"></app-header>
    <div class="list" style="padding-bottom: 5rem">
      <div
        v-for="(item, index) in list[0]"
        :key="item.app_id"
        class="ui-item">
        <div class="left">
          <div class="img">
            <img class="app" :src="item.icon_path">
          </div>
          <div class="info">
            <h2>{{item.name}}</h2>
            <p>{{desc[index]}}</p>
          </div>
        </div>
        <div class="right">
          <mt-button @click="downloadHanle(item.apk_path)" type="primary" style="font-size: 14px;margin-bottom: 10px;">Android下载</mt-button>
          <a style="font-size: 14px;line-height: 41px;" :href="item.ios_path" class="mint-button mint-button--default mint-button--normal">IOS下载</a>
        </div>
      </div>
      <p style="width: 100%;font-size: 1rem;text-align: center; padding: .5rem 0; color: red;">
        安卓用户请您点击右上角，使用浏览器打开进行下载
      </p>
    </div>
  </div>
</template>

<script>
  import AppHeader from '../../components/header/header'
  import {DownloadUrl} from '../../config/urls'
  import {isWechat} from '../../lib/util'

  export default {
    data () {
      return {
        list: [],
        desc: [
          '重卡汇司机版是专为红岩卡车司机量身定做的一款应用。司机用户注册后可享受多种贴心、便捷的服务，货车专用导航、故障诊断、行程分析、服务预约等一系列功能，能帮助司机朋友们对车随时掌控，对路精准定义，是司机朋友们的好帮手、好伴侣。',
          '重卡汇管理版是专为红岩车主以及红岩车队的管理者量身定做的一款应用。车主或车队管理者通过APP能够对车辆进行路线规划、实时监控，同时，APP中驾驶行为分析、车况报告、运行报表等数据服务，有助于大小车队科学化管车、用车，提高了车队管理信息化程度。',
          '重卡汇服务版是专为红岩服务站量身定做的一款应用，服务版APP为服务站和红岩用户建立了更多高效的链接，使司机朋友们的售后服务体验更加简单、轻松、省时。'
        ]
      }
    },
    components: {
      AppHeader
    },
    created () {
      this.getData()
    },
    methods: {
      getData () {
        this.$http.get(DownloadUrl.download, {params: {m: 1}}).then(data => {
          let res = data.data
          if (res.state) {
            this.list = res.data.list
          }
        })
      },
      downloadHanle (path) {
        if (isWechat()) {
          return alert('请您点击右上角，使用浏览器打开进行下载')
        }
        window.open(path)
      }
    }
  }
</script>

<style lang="less" scoped>
  h2{
    line-height: 24px;
    font-size: 16px;
    color: #111;
  }
  .wrap {
    width: 100%;
    height: 100%;
    background: #fff;
    padding-top: 60px;
    .ui-item{
      padding: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      border-bottom: 1px solid #eee;
      .left{
        flex: 1;
        display: flex;
        .img{
          max-height: 48px;
          width: 48px;
          img{
            width: 100%;
            height: 100%;
          }
        }
        .info{
          flex: 1;
          padding-left: 5px;
          h2{
            margin: 0;
          }
          p{
            color: #56697f;
            font-size: 12px;
            line-height: 15px;
            margin: 0;
          }
        }
      }
      .right{
        width: 80px;
      }
    }
  }
</style>



